
$backgroundColor: #0d1330;
$bodyColor: white;
$bodyFont: -apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
$bodyHeight: 700px;


body {
  background: $backgroundColor;
  color: $bodyColor;
  font-family: $bodyFont;
}

.sidebarNav{
  float: left;
  width: 90px;
  list-style-type: none;
  height: $bodyHeight; //I didnt do this by choice theres a bug that doesnt let you use percentages but this leaves bottom of sidebar blank without adding scrollbars for blank space.... its a bug or im dumb lol
  margin-left: -22px;
  a {
    color: white;
    text-decoration: none;
  }
}

.header-contact-links {
  text-decoration: none;
  color: white;
  float: right;
}

.projects-skills-button {
  align-items: center;
  background-color: #84e1ff;
  border: 0;
  border-radius: 100px;
  box-sizing: border-box;
  color: black;
  display: inline-flex;
  font-size: 14px;
  font-weight: 600;
  justify-content: center;
  line-height: 30px;
  min-height: 30px;
  width: 70px;
  overflow: hidden;
  padding: 0px;
  padding-left: 20px;
  padding-right: 20px;
  text-align: center;
  vertical-align: middle;
  margin-top: -3px;
}

.projects-background-button {
  align-items: center;
  background-color: rgba(75, 81, 83, .5);
  border: 0;
  border-radius: 6px;
  box-sizing: border-box;
  color: white;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  min-width: 500px;
  max-width: 1000px;
  min-height: 30px;
  overflow: hidden;
  padding: 0px;
  padding-left: 20px;
  padding-right: 20px;
  text-align: left;
  vertical-align: middle;
  a {
    color: white;
  }
}

.button-3 {
  appearance: none;
  background-color: #2ea44f;
  border: 1px solid rgba(27, 31, 35, .15);
  border-radius: 6px;
  box-shadow: rgba(27, 31, 35, .1) 0 1px 0;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-family: -apple-system,system-ui,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji";
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
  padding: 6px 16px;
  position: relative;
  text-align: center;
  text-decoration: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: middle;
  white-space: nowrap;
}